.App-logo {
  -webkit-animation: Banner-bounce infinite 1s cubic-bezier(.5, 0, 1, .5) alternate;
          animation: Banner-bounce infinite 1s cubic-bezier(.5, 0, 1, .5) alternate;
  height: 450px;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  padding-top: 100px;
  -ms-flex-pack: center;
      justify-content: center;
}

@-webkit-keyframes Banner-bounce {
  from { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
  to { -webkit-transform: translate3d(0, 400px, 0); transform: translate3d(0, 400px, 0); }
}

@keyframes Banner-bounce {
  from { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
  to { -webkit-transform: translate3d(0, 400px, 0); transform: translate3d(0, 400px, 0); }
}



